:root {
  color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  color: var(--color);
  box-sizing: border-box;
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-weight: 500;
  display: flex;
}

.is-active {
  color: var(--color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--color);
  cursor: pointer;
  text-decoration: underline;
}
